import React from "react";
import {
  Dialog,
  DialogContent,
  DialogTitle,
  Button,
  Typography
} from "@material-ui/core";
import { battlefield, nomansland } from "./Encounters";

function getRandomIntInclusive(dice,multiplier) {
  var randomNumber =  Math.floor(Math.random() * dice) + 1
  return randomNumber //* multiplier>0&&multiplier;
}
class App extends React.Component {
  constructor(props) {
    super(props);
  }
  state = {
    d100: null,
    rolltype: "none",
    action: ""
  };
  searchForRolls(roll, rolltype) {
    let string =
      rolltype === "battlefield"
        ? battlefield(roll).string
        : nomansland(roll).string;
    if (string.includes("&")) {
      let extracted = string.substring(
        string.indexOf("&") + 1,
        string.lastIndexOf("&")
      );
      let multiplier = 1;
      if (!isNaN(extracted.substring(0, extracted.indexOf("D")))) {


        multiplier = extracted.substring(0, extracted.indexOf("D"));

      }
      let array = extracted.replace(multiplier!==1&&multiplier+"D", "").split("+");
      // alert(multiplier + " Multiplier");
      // alert("D"+array[0]);
      // alert("+"+array[1]);
      let calculated = array[1]
        ? getRandomIntInclusive(array[0], multiplier) + parseInt(array[1])
        : getRandomIntInclusive(array[0], multiplier);

      let correctedString = string
        .replace(extracted, calculated)
        .replace("&", "")
        .replace("&", "");
      this.getAction(correctedString);
      //alert(correctedString);
    } else {
      this.getAction(string);
    }
  }
  getAction(string) {
    let d10 = getRandomIntInclusive(10);

    if (string.includes("[")) {
      let array = string.split(",");
      array.forEach(item => {
        if (
          item.substring(item.indexOf("[") + 1, item.indexOf("]")).includes("-")
        ) {
          let stringBegins = item.indexOf("]");
          let numbers = item
            .substring(
              parseInt(item.lastIndexOf("[") + 1),
              parseInt(item.lastIndexOf("]"))
            )
            .split("-");
          if (d10 >= parseInt(numbers[0]) && d10 <= parseInt(numbers[1])) {
            this.setState({
              action:
                string.substring(0, string.indexOf("[")) +
                " " +
                item.substr(item.lastIndexOf("("), item.lastIndexOf(")"))
            });
          }
        } else {
          let stringBegins = item.indexOf("]");
          let number = item.substring(
            parseInt(item.lastIndexOf("[") + 1),
            parseInt(item.lastIndexOf("]"))
          );
          if (d10 === parseInt(number)) {
            this.setState({
              action:
                string.substring(0, string.indexOf("[")) +
                " " +
                item.substr(item.lastIndexOf("("), item.lastIndexOf(")"))
            });
          }
        }
      });
    } else {
      this.setState({
        action: string
      });
    }
  }
  render() {
    return (
      <Dialog fullScreen={true} open={true}>
        <DialogContent>
          <DialogTitle style={{ textAlign: "center" }}>
            Generator zdarzeń OnlyWar{" "}
          </DialogTitle>
          <div style={{ width: "100%", textAlign: "center" }}>
            <Button
              onClick={() =>
                this.searchForRolls(getRandomIntInclusive(100), "battlefield")
              }
            >
              Rzuć kością (Pole bitwy)
            </Button>
            <Button
              onClick={() =>
                this.searchForRolls(getRandomIntInclusive(100), "nomansland")
              }
            >
              Rzuć kością (Ziemia niczyja)
            </Button>
          </div>
          <br />
          <br />
          <br />
          <br />
          <br />
          <br />
          <Typography style={{ textAlign: "center" }} variant="h4">
            {this.state.action && this.state.action}
          </Typography>
        </DialogContent>
      </Dialog>
    );
  }
}

export default App;
